import Vue from 'vue'
import Router from 'vue-router'
// import index from '@/views/index'
// import history from '@/views/history'
// import temple_explore from '@/views/temple_explore'
// import chiayi from '@/views/chiayi'

Vue.use(Router)
function lazyLoad(view){
  return() => import(`@/views/${view}.vue`)
}
export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'index',
      component: lazyLoad('index')
    },
    {
      path: '/history',
      name: 'history',
      component: lazyLoad('history')
    },
    {
      path: '/temple_explore',
      name: 'temple_explore',
      component: lazyLoad('temple_explore')
    },
    {
      path: '/chiayi',
      name: 'chiayi',
      component: lazyLoad('chiayi')
    },
    {
      path: '/VR',
      name: 'VR',
      component: lazyLoad('VR')
    },
    {
      path: '/tracking',
      name: 'tracking',
      component: lazyLoad('tracking')
    },
    {
      path: '/video',
      name: 'video',
      component: lazyLoad('video')
    }
  ]
})



